import React from 'react';

import RibbonPanel from './ui/RibbonPanel';
import SimplePanel from './ui/SimplePanel';
import List from './ui/List';

function ResumeContent(props) {

  const experience = [
    {id:1, title:'Secretary of Public Education (Government)', description:'Area Director of the General Directorate of Information Systems. Management of National Projects, Software Development (Java, AngularJS and ReacJS), Database Management (MySQL and SQL Server).'},
    {id:2, title:'Secretary of Public Education (Government)', description:'Area Director of the General Directorate of Information Systems. Management of National Projects, Software Development (Java, AngularJS and ReacJS), Database Management (MySQL and SQL Server).'},
    {id:3, title:'Secretary of Public Education (Government)', description:'Area Director of the General Directorate of Information Systems. Management of National Projects, Software Development (Java, AngularJS and ReacJS), Database Management (MySQL and SQL Server).'},
  ];

	return(
		<div>
			<RibbonPanel 
				title="William's Resume"
				ribbonColor="#111317"
			>
				<p className="resume-text">
					Develop myself professionally as part of a leading company in which I can contribute with my skills and knowledge in the systems area, always seeking to progress and improve the performance of my activities to achieve positions of greater responsibility and challenge. Offer solutions according to the expectations of the companies, creating opportunities for personal and professional development.
				</p>
				<div className="resume-content">
					<SimplePanel backgroundColor="#111317" color="#fff">
						<h2>Work experience</h2>
						<List 
							data={experience}
						/>
					</SimplePanel>

				</div>
			</RibbonPanel>
      <style jsx>{`
				.resume-text{
					font-size: 22px;
					text-align: justify;
					line-height: normal;
				}
				.resume-content{
					display: grid;
					grid-template-columns: repeat(2, 1fr);
				}
				@media only screen and (max-width : 768px) {
          .resume-text{
            font-size: 16px;
          }
        }
      `}</style>
    </div>
	);
}

export default ResumeContent;